import React, { Component } from 'react';
import ChartPage from '../../../components/ChartPage';
import * as d3 from 'd3';
import { AsyncStorage } from '../utils';

const ctable = [
  '#ACD8FF',
  '#FFBB78',
  '#98DF8A',
  '#FF9896',
  '#C5B0D5',
  '#C49C94',
  '#F7B6D2',
  '#DBDB8D',
]

const saveKey = 'ScatterPlot';

const d0 = `A\t15\t15\t15
B\t30\t20\t10
C\t30\t40\t8
D\t10\t30\t5
E\t20\t10\t1
`

export default class Page extends Component {
  constructor(props) {
    super(props);
    this.state = {
      text: this.props.text,
      dataSet: {
        data: [],
        xmin: 0,
        ymin: 0,
        xmax: 100,
        ymax: 100,
      },
    }
  }

  componentDidMount() {
    const text = AsyncStorage.load(saveKey, d0);
    this.onChangeText(text);
  }
  
  componentWillUnmount() {
  }

  componentDidUpdate() {
  }

  componentWillReceiveProps(nextProps) {
  }

  dataSet = () => {
    const r = [];
    const l = this.state.text.split('\n')
    let xmin = null;
    let xmax = null;
    let ymin = null;
    let ymax = null;
    let max = 1;
    let cmap = 0;
    for (var i in l) {
      if (l[i] == '') {
        cmap = (cmap + 1) % ctable.length;
      } else {
        const t = l[i].replace(/(\s+)/g, ' ').split(' ');
        if (t.length >= 3) {
          let value = 1;
          if (xmin === null || xmin > t[1]) xmin = Number(t[1]);
          if (ymin === null || ymin > t[2]) ymin = Number(t[2]);
          if (xmax === null || xmax < t[1]) xmax = Number(t[1]);
          if (ymax === null || ymax < t[2]) ymax = Number(t[2]);
          if (t.length >= 4) {
            value = Number(t[3])
          }
          if (max < value) max = value;
          r.push({
            label: t[0],
            x: t[1],
            y: t[2],
            value,
            cmap,
          })
        }
      }
    }
    let dx = (xmax - xmin)/5;
    //if (dx < 1) dx = 1;
    let dy = (ymax - ymin)/5;
    //if (dy < 1) dy = 1;
    xmin -= dx;
    ymin -= dy;
    xmax += dx;
    ymax += dy;
    return { data: r.map( v => {
      v.value /= max;
      return v;
    }), xmin, ymin, xmax, ymax, max, }; 
  }

  initialize = (svg, { width, height }) => {
    const { xmin, xmax, ymin, ymax } = this.state.dataSet;
    this.width = width;
    this.height = height;

    this.xScale = d3.scaleLinear()
      .domain([xmin, xmax])
      .range([0, width]);
    this.yScale = d3.scaleLinear()
      .domain([ymin, ymax])
      .range([0, height]);

    this.xAxis = d3.axisBottom(this.xScale)
    .ticks(10)
    .tickSize(width)
    .tickFormat(d => d)

    this.yAxis = d3.axisRight(this.yScale)
    .ticks(10)
    .tickSize(height)
    .tickFormat(d => d)

    this.base = svg.select('g.base')
    this.axis = svg.select('g.axis')

    this.zoomBehavior = d3.zoom()
      .scaleExtent([1 / 30, 3000])
      .on('start', () => {
      })
      .on('zoom', () => {
        const transform = d3.event.transform;
        this.transform = transform;
        this.updateSelf(svg);
      })
      .on('end', () => {
      })

    svg.call(this.zoomBehavior)

    this.setState({
      dataSet: this.dataSet(),
      height,
      width,
    })
  }

  update = (svg, { width, height }) => {
    const self = this;
    const { xmin, xmax, ymin, ymax, max } = this.state.dataSet;
    let rmax = 5;
    if (max > 1) rmax = width/10+3;
    this.width = width;
    this.height = height;

    this.rmax = rmax;

    this.xScale
      .domain([xmin, xmax])
      .range([0, width]);
    this.yScale
      .domain([ymin, ymax])
      .range([0, height]);

    const g = svg.select('g.base')

    g.selectAll('circle')
     .data(this.state.dataSet.data)
     .exit()
     .remove()

    g.selectAll('circle')
     .data(this.state.dataSet.data)
     .enter()
     .append('circle')
     .attr('cx', d => this.xScale(d.x))
     .attr('cy', d => this.yScale(d.y))
     .attr('fill', d => ctable[d.cmap])
     .attr('stroke', '#FFF')
     .attr('stroke-width', 2)
     .attr('fill-opacity', 0.4)
     .attr('r', d => d.value*this.rmax)

    g.selectAll('text.label')
     .data(this.state.dataSet.data)
     .exit()
     .remove()

    g.selectAll('text.label')
     .data(this.state.dataSet.data)
     .enter()
     .append('text')
     .classed('label', true)
     .attr('x', d => this.xScale(d.x))
     .attr('y', d => this.yScale(d.y)+5)
     .text(d => d.label)
     .attr('font-size', 10)
     .attr('text-anchor', 'middle')
     .attr('alignment-baseline', 'hanging')
     .attr('fill', 'black')
     .style('pointer-events', 'none')

     this.xAxis
      .scale(this.xScale)
     this.yAxis
      .scale(this.yScale)

    this.updateSelf(svg);
  }

  updateSelf = (svg) => {
    const self = this;
    const g = svg.select('g.base')
    const { width, height } = this;

    if (this.transform) {
      this.base.attr("transform", this.transform);
      this.axis.select('g.xAxis').call(this.xAxis.scale(this.transform.rescaleX(this.xScale)));
      this.axis.select('g.yAxis').call(this.yAxis.scale(this.transform.rescaleY(this.yScale)));
    }

    g.selectAll('circle')
     .attr('cx', d => this.xScale(d.x))
     .attr('cy', d => this.yScale(d.y))
     .attr('fill', d => ctable[d.cmap])
     .attr('r', d => d.value*this.rmax)

    g.selectAll('text.label')
     .attr('x', d => this.xScale(d.x))
     .attr('y', d => this.yScale(d.y)+5)
     .text(d => d.label) 

     this.xAxis
      .tickSize(height)
      .tickFormat(d => d)
     this.yAxis
      .tickSize(width)
      .tickFormat(d => d)

    this.axis.select('g.xAxis').remove()
    this.axis.select('g.yAxis').remove()

    function customXAxis(g) {
      g.call(self.xAxis);
      g.select('.domain').remove();
      g.selectAll('.tick line')
        .attr('stroke', '#777')
        .attr('stroke-dasharray', '2,2');
      g.selectAll('.tick text')
        .attr('text-anchor','start')
        .attr('transform',`rotate(${-90},${10},${height-15})`)
        .attr('x',10)
        .attr('y', height-15)
        .attr('dx', -8);
    }

    this.axis.append('g')
      .classed('xAxis', true)
      .call(customXAxis);

    function customYAxis(g) {
      g.call(self.yAxis);
      g.select('.domain').remove();
      g.selectAll('.tick line')
        .attr('stroke', '#777')
        .attr('stroke-dasharray', '2,2');
      g.selectAll('.tick text')
        .attr('text-anchor','start')
        .attr('x', 10)
        .attr('dy', -4);
    }

    this.axis.append('g')
      .classed('yAxis', true)
      .call(customYAxis);
  }

  onChangeText = (text) => {
    this.setState({ text, }, () => {
      AsyncStorage.save(saveKey, text);
      this.setState({ dataSet: this.dataSet() }, () => {
      })
    });
  }

  render() {
    return (
      <ChartPage
        title={this.props.title}
        initialize={this.initialize}
        update={this.update}
        onChangeText={this.onChangeText}
        text={this.state.text}
      />
    )
  }
}

Page.defaultProps = {
  title: '散布図',
  text: d0,
}
